import styled from "styled-components"
import { Theme } from "../../constants/types"
import theme from "../../constants/theme"
type Props = {
  css?: any
  color?: string
  theme: any
  children: any
}
export default styled.h4`
  font-size: 2.4rem;
  color: ${(props: Props) =>
    props.color
      ? props.theme.colors[`${props.color}`]
      : props.theme.colors.default};
`
