import React from "react"
import { Link } from "gatsby"
import styled from "styled-components"
import { Theme } from "../../constants/types"
import theme from "../../constants/theme"
import { rgba, tint } from "polished"
type Props = {
  color: string
  capital?: boolean
  css?: any
  theme: any
  to: string
  tab?: string
}
export default styled(props => <Link {...props} />)`
  text-transform: ${(props: Props) =>
    props.capital ? "uppercase" : "capitalize"};
  color: ${(props: Props) =>
    props.color
      ? props.theme.colors[`${props.color}`]
      : props.theme.colors['default']};
  text-decoration: none;
  padding: ${(props: Props) => (props.tab ? ".5rem 1rem" : "0")};
  border-radius: ${(props: Props) => (props.tab ? "2px" : "0")};
  border-left: ${(props: Props) => (props.tab ? "4px solid" : "0")};
  background-color: ${props =>
    props.tab
      ? `${rgba(`${props.theme.colors[`${props.color}`]}`, 0.2)}`
      : "none"};
  font-size: ${(props: Props) => (props.tab ? "1.2rem" : "1.6rem")};
`
