import styled from "styled-components"
import { Theme } from "../../constants/types"
import theme from "../../constants/theme"

import { rgba, tint } from "polished"
enum ButtonColor {
  primary,
  default,
  success,
  warning,
  danger,
}
type Props = {
  color: string
  capital?: boolean
  css?: any
  theme: any
}
export default styled.button`
  border: none;
  display: inline-block;
  width: auto;
  height: auto;
  text-transform: ${(props: Props) =>
    props.capital ? "uppercase" : "capitalize"};
  background-color: ${(props: Props) => props.theme.colors[`${props.color}`]};
  color: ${theme.colors.white};
  padding: 1rem 2.4rem;
  border-radius: 4px;
  letter-spacing: 1.5px;
  transition: 0.3s ease;
  font-family: ${theme.font.body};
  font-weight: ${theme.fontWeight.body};
  font-size: 1.6rem;

  &:hover {
    background-color: ${props =>
      `${tint(0.1, `${props.theme.colors[`${props.color}`]}`)}`};
    box-shadow: -1px 1px 5px
      ${props => `${rgba(`${props.theme.colors[`${props.color}`]}`, 0.4)}`};
    transform: translateY(-1px);
    outline: 0;
  }
  &:focus {
    outline: 0;
  }
`
