import React from "react"
import PropTypes from "prop-types"
import styled from "styled-components"
import theme from "../../constants/theme"
import { H2, Link } from "../index"
import { rgba } from "polished"

type ImageCardProps = {
  bgUrl: string
  url: string
  logo: string
  number: number
  title: string
  color?: string
}

type CardProps = {
  bgUrl: string
  color?: string
  theme: any
}
type LogoProps = {
  logo: string
}

export default function ImageCard({
  bgUrl,
  url,
  title,
  logo,
  number,
  color,
}: ImageCardProps) {
  return (
    <Link to={url}>
      <Card bgUrl={bgUrl} color={color}>
        <div>{number} Posts</div>
        <Logo logo={logo}></Logo>
        <Title>
          <H2>{title}</H2>
        </Title>
      </Card>
    </Link>
  )
}

ImageCard.propTypes = {
  logo: PropTypes.string.isRequired,
  number: PropTypes.number.isRequired,
  bgUrl: PropTypes.string.isRequired,
  url: PropTypes.string.isRequired,
  title: PropTypes.string.isRequired,
  color: PropTypes.string,
}
const Title = styled.div`
  align-self: start;
`
const Logo = styled.div`
  justify-self: flex-end;
  height: 100px;
  width: 100px;
  background-image: url(${(props: LogoProps) => props.logo});
  background-position: center center;
  background-size: cover;
`
const Card = styled.div`
  border: none;
  display: grid;
  grid-template-rows: 1fr minmax(0.5fr, auto);
  grid-template-columns: auto 1fr;
  width: auto;
  min-height: 250px;
  background-image: url(${(props: CardProps) => props.bgUrl});
  background-position: center center;
  background-size: cover;
  color: ${(props: CardProps) =>
    props.color
      ? props.theme.colors[`${props.color}`]
      : props.theme.colors.white};
  padding: 1rem 2.4rem;
  border-radius: 8px;
  letter-spacing: 1.5px;
  transition: 0.3s ease;
  font-family: ${theme.font.body};
  font-weight: ${theme.fontWeight.body};
  font-size: 1.6rem;
  box-shadow: 0px 4px 16px ${rgba(`${theme.colors.default}`, 0.2)};

  &:hover {
    transform: translateY(-2px) scale(1.05);
    outline: 0;
  }
  &:focus {
    outline: 0;
  }
`
