const GlobalStyle = createGlobalStyle`
  * {
  margin: 0;
  padding: 0;
  
}
  html {
    font-size: 10px;
    font-family: ${font.font.body};
    font-weight: ${font.fontWeight.body};
    color: ${colors.default};
    line-height: 1.6;

    @media ${sizes.breakpoints.xs} {
      font-size: 8px;
    /* background-color: green; */
    }
    @media ${sizes.breakpoints.sm} {
        font-size: 8px;
      /* background-color: green; */
    }
    @media ${sizes.breakpoints.md} {
      font-size: 10px;
      /* background-color: yellow; */
    }
    @media ${sizes.breakpoints.lg} {
      font-size: 10px;
      /* background-color: pink; */
    }
    @media ${sizes.breakpoints.xl} {
      font-size: 12px;
      /* background-color: red; */
    } 
  }
/* 
  main {
    padding-left: 2rem;
    padding-right: 2rem
  } */

  body{
    font-size: 1.6rem
  }

  h1,h2,h3,h4,h5,h6 {
      font-family: ${font.font.heading};
      font-weight: ${font.fontWeight.heading}
  }


`
